import classNames from "classnames";
import React, { InputHTMLAttributes } from "react";

interface BaseInputProps extends InputHTMLAttributes<HTMLInputElement> {
  label: string;
}

export const BaseInput: React.FC<BaseInputProps> = ({
  id,
  type,
  name,
  label,
  value,
  onChange,
  placeholder,
  required = false,
}) => {

  const labelStyle = classNames("text-sm font-bold")
  const inputStyle = classNames(
    "w-full py-2 px-3 bg-transparent",
    "border-2 border-gray-300 rounded-lg",
    "placeholder-gray-300",
    "hover:ring-blue-400 hover:ring hover:border-transparent",
    "focus:ring-blue-400 focus:ring focus:border-transparent",
    "transition-all duration-300 ease-in-out",
    "appearance-none outline-none"
  )

  return (
    <div className="flex flex-col  gap-2 w-full h-full  justify-center items-start">
      <label className={labelStyle} htmlFor={id}>
        {label}
      </label>
      <input
        className={inputStyle}
        id={id}
        type={type}
        name={name}
        value={value}
        onChange={onChange}
        placeholder={placeholder}
        required={required}
      />
    </div>
  );
};
