"use client";
import React, { useState } from "react";
import { useRouter } from "next/navigation";
import api from "@/services/backend-api/authApi";
import { BaseInput } from "../ui/Input/Input";

export const AuthForm: React.FC = () => {
  const router = useRouter();
  const [formData, setFormData] = useState({
    email: "",
    password: "",
  });
  const [error, setError] = useState<string | null>(null);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      const data = await api.signin(formData);
      localStorage.setItem("userToken", data.auth_token);
      router.push("/");
    } catch (error) {
      setError(error as string);
      console.error(error);
    }
  };
  const handleRegister = () => {
    router.push("/auth/sign_up");
  };

  return (
    <div className="bg-white border border-gray-300 px-10 pb-10 pt-5 rounded-xl">
      <div className="flex flex-col items-center justify-center max-w-70">
        <h2 className="text-2xl font-bold mb-5">Вход</h2>
        <form onSubmit={handleSubmit} className="flex flex-col gap-3">
          <BaseInput
            label="Email"
            type="email"
            id="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
          />
          <BaseInput
            label="Пароль"
            id="password"
            type="password"
            name="password"
            value={formData.password}
            onChange={handleChange}
          />

          {error && (
            <div className="text-red-500 text-xs italic mb-4">{error}</div>
          )}
          <div className="flex gap-4 mt-2 justify-between">
            <button
              className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
              type="submit"
            >
              Войти
            </button>
            <button
              className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
              type="button"
              onClick={handleRegister}
            >
              Зарегистрироваться
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};
